body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #121212;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 150rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

body {
  background: url("/static/images/wall-08.webp") 50% 0 no-repeat;
  background-position: top;
  height: 100vh;
  width: 100vw;
  position: relative;
  padding: 2rem 0;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.header {
  margin-top: 1rem;
  position: absolute;
  display: flex;
  justify-content: space-around;
  width: 100vw;
  top: 0;
}
.header .topbar {
  border: 1px solid #111;
  border-radius: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10rem;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 0;
}
.header .topbar p {
  margin: 0 0.3rem;
  z-index: 1;
}
.header .place {
  padding: 0 5rem;
}
.header .place p {
  margin: 0 2rem;
  font-size: 1rem;
  color: #ffb956;
}
.header .place .current {
  font-size: 2rem;
  color: #677eca;
}
.header .home {
  padding: 0 3rem;
}
.header .home p {
  color: #677eca;
}
.header .blog-contact {
  padding: 0 5rem;
  width: 20%;
}
.header .about {
  cursor: pointer;
}

.terminal {
  display: none;
  margin: 0 auto;
  width: 40%;
  height: 50%;
  top: 7rem;
  left: 5rem;
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  cursor: grab;
  border-radius: 1rem;
}
.terminal .bar {
  width: 100%;
  height: 5rem;
  background-color: #111;
  border-radius: 1rem 1rem 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.terminal .bar p {
  margin: 0 2rem;
}
.terminal .bar .buttons {
  display: flex;
}
.terminal .bar .buttons p {
  margin: 0 1rem;
  cursor: pointer;
}
.terminal .bar .buttons .close:hover {
  color: #df2b2b;
}
.terminal .bar .buttons .max:hover {
  color: #ffb956;
}
.terminal .bar .buttons .restore:hover {
  color: #71B100;
}
.terminal .bar .buttons .min:hover {
  color: #677eca;
}

.maximize {
  width: 97vw;
  height: 90vh;
  position: initial !important;
  cursor: auto;
  margin: 0 auto;
}

.minimize {
  width: 20vw;
  height: 5vh;
  position: initial !important;
  cursor: auto;
}